.eventBoxPoor{
    background-image: url('../assets/poor.png');
    background-size: cover;
    background-position: center;
}

.eventBoxNormal{
    background-image: url('../assets/decent.png');
    background-size: cover;
    background-position: center;
}

.eventBoxGood{
    background-image: url('../assets/good.png');
    background-size: cover;
    background-position: center;
}

.topFlex{
    display: flex;
    justify-content: space-between;
    padding: 1rem;
}
.day{
    color: whitesmoke;
}

.event{
    max-width: 20rem;
    border: 1px solid gray;
    border-radius: 1rem;
    padding: 1rem;
    background-color: whitesmoke;
}

.bottomFlex{
    display: flex;
    justify-content: space-between;
    padding: 1rem;
}

.commentBox{
    width: 15rem;
    border: 1px solid gray;
    border-radius: 1rem;
    padding: 1rem;
    background-color: whitesmoke;
}

.comment{
    display: flex;
    justify-content: left;
    align-items: center;
}

.userPic{
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 1rem;
}

.selection{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.btn{
    margin-bottom: 1rem;
    width: 15rem;
    padding: 0.7rem;
    font-size: 1rem;
}

.ending{
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10%;
}